import { boleUtils } from "./core";

const config = (function () {
	let module = {};
	let self = module;

	module.PROPERTIES = {};

	module.init = (properties) => {
		self.PROPERTIES = properties;
	};

	/*** PROPERTIES ***/

	/* Environment */
	module.getEnvironment = () => {
		return self.PROPERTIES.ENVIRONMENT;
	};

	/* Files */
	module.getFilesMaxUpload = ()=> {
    return self.PROPERTIES.FILES.MAX_UPLOAD;
  };
  module.getFilesErrorType = ()=> {
    return self.PROPERTIES.FILES.ERROR_FILE_TYPE;
  }
  module.getFilesTypes = ()=> {
    return self.PROPERTIES.FILES.TYPES;
  };
  module.getFilesErrorSizeMsg = ()=> {
    return self.PROPERTIES.FILES.ERROR_SIZE_MSG;
  };
  module.getFilesErrorNameMsg = ()=> {
    return self.PROPERTIES.FILES.ERROR_NAME_MSG;
  };
  module.getFilesDeleteMsgError = ()=> {
    return self.PROPERTIES.FILES.DELETE_MSG_ERROR;
  };
	/* Partícula que se añade en el mensaje cuando
	enviamos un archivo para saber que es un archivo */
	module.getStringFileToserver = () => {
		return 'Archivo: ';
	};
	/* End Files */

	/* Socket */
	module.getUrlSocketDev = () => {
		return self.PROPERTIES.SOCKET.URL_DEV;
	};
	module.getUrlSocketEpr = () => {
		return self.PROPERTIES.SOCKET.URL_EPR;
	};
	module.getUrlSocketEprMobile = () => {
		return self.PROPERTIES.SOCKET.URL_EPR_MOBILE;
	};
	module.getSocketPrefixPathDev = () => {
		return self.PROPERTIES.SOCKET.PREFIX_PATH_DEV;
	};
	module.getSocketPrefixPathEpr = () => {
		return self.PROPERTIES.SOCKET.PREFIX_PATH_EPR;
	};

	module.getUrlSocket = () => {
		switch (self.getEnvironment()) {
			case 'mock':
				return '../';
			case 'dev':
				return self.getUrlSocketDev();
			case 'epd':
				return self.getUrlSocketDev();
			case 'epi1':
				return self.getUrlSocketDev();
			case 'epi2':
				return self.getUrlSocketDev();
			case 'piloto':
				return boleUtils.checkIsMobile() ? self.getUrlSocketEprMobile() : self.getUrlSocketEpr();
			case 'epr':
				return boleUtils.checkIsMobile() ? self.getUrlSocketEprMobile() : self.getUrlSocketEpr();
			default:
				console.error('error: not environment selected!');
		}
	};

	module.getSocketPrefixPath = () => {
		switch (self.getEnvironment()) {
			case 'mock':
				return '../';
			case 'dev':
				return self.getSocketPrefixPathDev();
			case 'epd':
				return self.getSocketPrefixPathDev();
			case 'epi1':
				return self.getSocketPrefixPathDev();
			case 'epi2':
				return self.getSocketPrefixPathDev();
			case 'piloto':
				return self.getSocketPrefixPathEpr();
			case 'epr':
				return self.getSocketPrefixPathEpr();
			default:
				console.error('error: not environment selected!');
		}
	};
	/* End Socket */

	/* Api Rest */
	module.getBaseApiBackFiles = ()=> {
	  switch ( self.getEnvironment() ) {
		case 'mock':
		  return '../';
		case 'dev':
		  return self.PROPERTIES.ROUTES.BASE_API_BACK_DEV;
		case 'epd': 
		  return self.PROPERTIES.ROUTES.BASE_API_BACK_EPD;
		case 'epi1': 
		  return self.PROPERTIES.ROUTES.BASE_API_BACK_EPI1;
		case 'epi2': 
		  return self.PROPERTIES.ROUTES.BASE_API_BACK_EPI2;
		case 'piloto': 
		  return self.PROPERTIES.ROUTES.BASE_API_BACK_EPR;
		case 'epr': 
		  return self.PROPERTIES.ROUTES.BASE_API_BACK_EPR;
		default:
		  console.error('error: not environment selected!');
	  }    
	};
	/* End Api Rest */

	return {
		init: module.init,
		getEnvironment: module.getEnvironment,
		getUrlSocketDev: module.getUrlSocketDev,
		getUrlSocketEpr: module.getUrlSocketEpr,
		getUrlSocketEprMobile: module.getUrlSocketEprMobile,
		getSocketPrefixPathDev: module.getSocketPrefixPathDev,
		getSocketPrefixPathEpr: module.getSocketPrefixPathEpr,
		getUrlSocket: module.getUrlSocket,
		getSocketPrefixPath: module.getSocketPrefixPath,
		getBaseApiBackFiles: module.getBaseApiBackFiles,
    getFilesMaxUpload: module.getFilesMaxUpload,
    getFilesTypes: module.getFilesTypes,
    getFilesErrorType: module.getFilesErrorType,
    getFilesErrorSizeMsg: module.getFilesErrorSizeMsg,
    getFilesErrorNameMsg: module.getFilesErrorNameMsg,
    getFilesDeleteMsgError: module.getFilesDeleteMsgError,
		getStringFileToserver: module.getStringFileToserver
	};

})();

export default config;
