import {
	UPDATE_COUNT,
	NOT_NEWS_LOADING,
	NOT_NEWS_UPDATE,
	NOT_NEWS_DELETE_ELEMENT,
	NOT_NEWS_UPDATE_COUNT,
	NOT_NEWS_FAILURE,
	NOT_CHAT_LOADING,
	NOT_CHAT_UPDATE,
	NOT_CHAT_UPDATE_COUNT,
	NOT_CHAT_FAILURE,
	NOT_PENDING_LOADING,
	NOT_PENDING_UPDATE,
	NOT_PENDING_UPDATE_COUNT,
	NOT_PENDING_FAILURE
} from '../actions/types';
import { boleUtils } from '../core/';
  
const initialState = {};
let objAux = {};

initialState.count = 0;

/* Novedades y notificaciones */
initialState.notNews = {};
initialState.notNews.loading = false;
initialState.notNews.error = boleUtils.resetDataError();
initialState.notNews.data = [];
initialState.notNews.count = 0;

/* Chat */
initialState.notChat = {};
initialState.notChat.loading = false;
initialState.notChat.error = boleUtils.resetDataError();
initialState.notChat.show = false;
initialState.notChat.count = 0;

/* Firma y documentación pendiente */
initialState.notPending = {};
initialState.notPending.loading = false;
initialState.notPending.error = boleUtils.resetDataError();
initialState.notPending.show = false;
initialState.notPending.count = 0;

const notification = (state = initialState, action = {}) => {

	switch (action.type) {
		case UPDATE_COUNT:
			return Object.assign({},  state, { count: action.payload } );

		case NOT_NEWS_LOADING:
			objAux = {};
			objAux.loading = true;
			objAux.error = boleUtils.resetDataError();
			objAux.data = state.notNews.data;
			return Object.assign({},  state, { notNews: objAux } );
		case NOT_NEWS_UPDATE:
			objAux = {};
			objAux.loading = false;
			objAux.data = [...state.notNews.data, ...action.payload];
			return Object.assign({},  state, { notNews: objAux } );
		case NOT_NEWS_DELETE_ELEMENT:
			objAux = {};
			objAux.loading = false;
			objAux.data = [...state.notNews.data].filter((v,i) => i !== action.payload);
			return Object.assign({},  state, { notNews: objAux } );
		case NOT_NEWS_UPDATE_COUNT:
			objAux = {};
			objAux.loading = false;
			objAux.count = action.payload;
			objAux.data = state.notNews.data;
			return Object.assign({},  state, { notNews: objAux } );
		case NOT_NEWS_FAILURE:
			objAux = {};
			objAux.loading = false;
			objAux.error = action.payload;
			objAux.data = state.notNews.data;
			return Object.assign({},  state, { notNews: objAux } );

		case NOT_CHAT_LOADING:
			objAux = {};
			objAux.loading = true;
			objAux.error = boleUtils.resetDataError();
			return Object.assign({},  state, { notChat: objAux } );
		case NOT_CHAT_UPDATE:
			objAux = {};
			objAux.loading = false;
			objAux.show = action.payload;
			return Object.assign({},  state, { notChat: objAux } );
		case NOT_CHAT_UPDATE_COUNT:
			objAux = {};
			objAux.loading = false;
			objAux.count = action.payload;
			return Object.assign({},  state, { notChat: objAux } );
		case NOT_CHAT_FAILURE:
			objAux = {};
			objAux.loading = false;
			objAux.error = action.payload;
			return Object.assign({},  state, { notChat: objAux } );

		case NOT_PENDING_LOADING:
			objAux = {};
			objAux.loading = true;
			objAux.error = boleUtils.resetDataError();
			return Object.assign({},  state, { notPending: objAux } );
		case NOT_PENDING_UPDATE:
			objAux = {};
			objAux.loading = false;
			objAux.show = action.payload;
			return Object.assign({},  state, { notPending: objAux } );
        case NOT_PENDING_UPDATE_COUNT:
            objAux = {};
            objAux.loading = false;
            objAux.count = action.payload;
            return Object.assign({},  state, { notPending: objAux } );
		case NOT_PENDING_FAILURE:
			objAux = {};
			objAux.loading = false;
			objAux.error = action.payload;
			return Object.assign({},  state, { notPending: objAux } );

		default:
			return state;
	}
}

export default notification;