import axios from 'axios';
import { boleRest, boleUtils } from '../core';
import {
    SIGN_LOADING,
    SIGN_SUCCESS,
    SIGN_FAILURE
} from './types';

export const sign = (function () {
    let module = {};
    let self = module;

    module.signLoading = () => {
        return {
            type: SIGN_LOADING
        }
    };

    module.signSuccess = (data) => {
        return {
            type: SIGN_SUCCESS,
            payload: data
        }
    };

    module.signFailure = (data) => {
        return {
            type: SIGN_FAILURE,
            payload: data
        }
    };

    module.getFilesSign = (clientId) => {

        return (dispatch) => new Promise((resolve, reject) => {
            let url = boleRest.getUrlFilesSign(clientId);
            dispatch(self.signLoading());

            axios.get(url).then(response => {
                dispatch(self.signSuccess(response.data));
                resolve();
            })
            .catch((error) => {
                console.log('error getFilesSign', error);
                let errorData = {
                    hasError: true,
                    msg: boleUtils.setMsgError(error.response ? error.response.status : '')
                };
                dispatch(self.signFailure(errorData));
            });
        });
    };

    return {
        signLoading: module.signLoading,
        signSuccess: module.signSuccess,
        signFailure: module.signFailure,
        getFilesSign: module.getFilesSign
    };

})();
