import {
  MODAL_SHOW_ERROR,
  MODAL_HIDE,
  MODAL_SHOW_CONFIRM,
  MODAL_HIDE_CONFIRM,
  BUTTONS_UPDATE_COUNT
} from './types';

export const ui = (function() {
  let module  = {};
  let self    = module;
  
  /* Si 'typeError' trae el valor de 'generalError', el modal aparecerá de forma global */
  module.showModalError = (msg, error, typeError) => {
    return {
      type: MODAL_SHOW_ERROR,
      payload: msg,
      errorResponse: error,
      typeError: typeError || 'error'
    }
  };

  module.hideModal = () => {
    return {
      type: MODAL_HIDE
    }
  };

  module.showModalConfirm = (data) => {
    return {
      type: MODAL_SHOW_CONFIRM,
      payload: data.msg,
      typeConfirm: data.typeConfirm,
      id: data.id
    }
  };

  module.hideModalConfirm = () => {
    return {
      type: MODAL_HIDE_CONFIRM
    }
  };

  module.buttonsUpdateCount = (type, count) => {
    return {
      type: BUTTONS_UPDATE_COUNT,
      payload: {
        type: type,
        count: count
      }
    }
  };

  return {
    showModalError:       module.showModalError,
    showModalConfirm:     module.showModalConfirm,
    hideModal:            module.hideModal,
    hideModalConfirm:     module.hideModalConfirm,
    buttonsUpdateCount:   module.buttonsUpdateCount
  };

} )();
