import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import {
	SignPending,
	AttachFiles
} from '../../../components/';
import { 
	panel
} from '../../../actions';
class PanelView extends Component {

	constructor(props) {
		super(props);
		
		this.hidePanel = this.hidePanel.bind(this);
	}

	hidePanel() {
		this.props.panelHide();
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				{/* Pantalla 06A1 TAREAS PENDIENTES AGRUPADAS FIRMAR - 5 --> El panel de la derecha que pone FIRMAS PENDIENTES */}
				<div id='panel-container'>
					<p onClick={ this.hidePanel }>X</p>
					{
						this.props.panel.type === 'sign' &&
						<SignPending />
					}
					{
						this.props.panel.type === 'file' &&
						<AttachFiles />
					}
				</div>
			</CSSTransitionGroup>
		);
	}
}

PanelView.propTypes = {

};

const mapStateToProps = state => {
	return {
		panel: state.panel
	};
};

const mapDispatchToProps = dispatch => ({
	panelHide: bindActionCreators(panel.panelHide, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(PanelView);
