import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { 
	sign
} from '../../../actions';
import {
	ButtonNav,
	FileSign
} from '../../../components/';

class SignPending extends Component {

	constructor (props) {
		super(props);
		this.state = {
			signSended: false
		};
	}

	componentWillMount() {
		this.props.getFilesSign();
	}

	// TODO. Habrá que juntar en un array todos los documentos que estén para firmar (checkbox = true)
	handleClickSignFile(value, fileId) {
		console.log('value: ', value);
		console.log('fileId: ', fileId);
	}

	/* Método lanzado al pulsar sobre Firmar los fichero seleccionados */
	signFiles() {
		console.log('Ficheros firmados');
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='sign-pending'>
					<div className='sign-pending-header'>
						<p>FIRMAS PENDIENTES</p>
					</div>
                    <div className='sign-pending-body'>
						{
							this.props.sign && this.props.sign.data && 
							this.props.sign.data.map((element, index) => 
							<FileSign 
								key = { index }
								data = { element }
								handleClick = { this.handleClickSignFile }
							/>)
						}
						{/* Código para mandar los ficheros */}
						{
							!this.state.signSended &&
							<div>
								<span>Introduce tu código</span>
								<input type="text" />
							</div>
						}
						{/* Mensaje de ficheros enviados */}
						{
							this.state.signSended &&
							<div>
								<span>Tus transferencias se han realizado correctamente</span>
								<button>ENVIAR EMAIL</button>
								<button>IMPRIMIR</button>
							</div>
						}
                    </div>
					{
						!this.state.signSended &&
						<div className='sign-pending-footer'>
							<ButtonNav 
								disable = { false }
								text = 'Firmar 3 ficheros'
								handleClick = { this.signFiles }
							/>
						</div>
					}
				</div>
			</CSSTransitionGroup>
		);
	}
}

SignPending.propTypes = {
	
};

const mapStateToProps = state => {
	return {
		sign: state.sign
	};
};

const mapDispatchToProps = dispatch => ({
	getFilesSign: bindActionCreators(sign.getFilesSign, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(SignPending);
