import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { 
	clientChat,
	panel
} from '../../../actions';
import {
	ButtonNav
} from '../../../components/';

class FileSign extends Component {

	constructor (props) {
        super(props);
        this.state = {
            valueCheckbox: false
        };
		this.handleChangeCheckbox = this.handleChangeCheckbox.bind(this);
    }

    componentWillMount() {
        this.setState({valueCheckbox: this.props.value});
    }
    
    handleChangeCheckbox(event) {
        this.setState({valueCheckbox: !this.state.valueCheckbox}, function() {
            this.props.handleClick(this.state.valueCheckbox, this.props.data.fileId);
        });
    }

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='file-sign'>
					<div className='file-sign-header'>
						<p> { this.props.data.title }</p>
					</div>
                    <div className='file-sign-body'>
                        {
                            !this.props.data.sign &&
                            <div>
                                <span>Cuenta de cargo: </span><span>{ this.props.data.account }</span>
                                <span>Importe: </span><span>{ this.props.data.amount } EUR</span>
                                <input 
                                    type = 'checkbox'
                                    className = 'bole-switch-input' 
                                    onChange = { this.handleChangeCheckbox }
                                    checked = { this.state.valueCheckbox ? 'checked' : '' }
                                />
                                <br /><br />
                            </div>
                        }
                        {
                            this.props.data.sign &&
                            <div>
                                <span>Importe: </span><span>{ this.props.data.amount } EUR</span>
                                <span>Fecha: </span><span>{ this.props.data.date }</span>
                                <span>Referencia: </span><span>{ this.props.data.ref }</span>
                                <br /><br />
                            </div>
                        }
                    </div>
				</div>
			</CSSTransitionGroup>
		);
	}
}

FileSign.propTypes = {
    data: PropTypes.object,
    handleClick: PropTypes.func,
    value: PropTypes.bool
};

const mapStateToProps = state => {
	return {
        sign: state.sign
    };
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(FileSign);
