import {
    MODAL_SHOW_ERROR,
    MODAL_SHOW_CONFIRM,
    MODAL_HIDE,
    MODAL_HIDE_CONFIRM,
    BUTTONS_UPDATE_COUNT
  } from '../actions/types';
  
  const initialState = {};
  
  initialState.modal = {};
  initialState.modal.show = false;
  initialState.modal.type = 'error';
  initialState.modal.msg  = '';
  initialState.modalConfirm = {};
  initialState.modalConfirm.show = false;
  initialState.modalConfirm.msg  = '';
  
  const auxButtons = {
  
    updateCount: (oldState, type, count)=> {
      let buttons = oldState.mainButtons.map( (item)=> {
        if ( item.type === type ) {
          item.count = count;
        }
        return item;
      });
  
      return buttons;
    }
  
  };
  
  const ui = (state = initialState, action = {}) => {
    let newButtons;
  
    switch (action.type) {
      case MODAL_SHOW_ERROR:
        let objError = {};
  
        objError.show   = true;
        objError.type   = action.typeError; 
        objError.msg    = action.payload;
        objError.error  = action.errorResponse;
  
        return Object.assign({},  state, { modal: objError} );
  
      case MODAL_SHOW_CONFIRM:
        let objConfirm = {};
  
        objConfirm.show         = true;
        objConfirm.msg          = action.payload;
        objConfirm.typeConfirm  = action.typeConfirm;
        objConfirm.id           = action.id;
  
        return Object.assign({},  state, { modalConfirm: objConfirm} );
  
      case MODAL_HIDE:
        let objReset = {};
  
        objReset.show = false;
        objReset.type = ''; 
        objReset.msg  = '';
  
        return Object.assign({},  state, { modal: objReset} );
  
      case MODAL_HIDE_CONFIRM:
        let objConfirmReset = {};
  
        objConfirmReset.show = false;
        objConfirmReset.msg  = '';
  
        return Object.assign({},  state, { modalConfirm: objConfirmReset} );
      
      case BUTTONS_UPDATE_COUNT:
        newButtons =  auxButtons.updateCount(state, action.payload.type, action.payload.count);
        return Object.assign({},  state, { mainButtons: newButtons} );
  
      default:
        return state;
    }
  }
  
  export default ui;