import {
	CLIENT_CHAT_VIEW_LOADING,
	CLIENT_CHAT_VIEW_UPDATE,
	CLIENT_CHAT_VIEW_FAILURE,
	CLIENT_CHAT_VIEW_SUCCESS,
	PANEL_VIEW_HIDE,
	UPDATE_CHAT_LOADING,
	UPDATE_CHAT_SUCCESS,
	UPDATE_CHAT_FAILURE
} from '../actions/types';
import { boleUtils } from '../core/';
  
const initialState = {};

initialState.loading = false;
initialState.loadingUpdate = false;
initialState.error = boleUtils.resetDataError();
initialState.errorUpdate = boleUtils.resetDataError();
initialState.show = false;
initialState.data = [];

const clientChat = (state = initialState, action = {}) => {

	switch (action.type) {
		case CLIENT_CHAT_VIEW_LOADING:
			return Object.assign({},  state, { error: boleUtils.resetDataError(), loading: true} );
		case CLIENT_CHAT_VIEW_UPDATE:
			return Object.assign({},  state, { show: action.payload, loading: false } );
		case CLIENT_CHAT_VIEW_FAILURE:
			return Object.assign({},  state, { error: action.payload, loading: false } );
		case CLIENT_CHAT_VIEW_SUCCESS:
			return Object.assign({},  state, { data: action.payload, loading: false } );
		case PANEL_VIEW_HIDE:
			return Object.assign({},  state, { loading: false, show: false, error: boleUtils.resetDataError() } );
		case UPDATE_CHAT_LOADING:
			return Object.assign({},  state, { errorUpdate: boleUtils.resetDataError(), loadingUpdate: true} );
		case UPDATE_CHAT_SUCCESS:
			return Object.assign({},  state, { data: [...state.data, action.payload], loadingUpdate: false} );
		case UPDATE_CHAT_FAILURE:
			return Object.assign({},  state, { errorUpdate: action.payload, loadingUpdate: false} );

		default:
			return state;
	}
}

export default clientChat;