import {
	HOME_VIEW_LOADING,
	HOME_VIEW_UPDATE,
	HOME_VIEW_FAILURE,
	HOMELIST_VIEW_LOADING,
	HOMELIST_VIEW_UPDATE,
	HOMELIST_VIEW_FAILURE
} from './types';

export const home = (function() {
  let module  = {};
	let self    = module;
	
	module.homeLoading = () => {
    return {
      type: HOME_VIEW_LOADING
    }
	};
	
	module.homeUpdate = (data) => {
    return { 
      type: HOME_VIEW_UPDATE,
      payload: data
    }
  };
  
  module.homeFailure = (data) => {
    return {
      type: HOME_VIEW_FAILURE,
      payload: data
    }
  };
	
	module.homeListLoading = () => {
    return {
      type: HOMELIST_VIEW_LOADING
    }
	};
	
	module.homeListUpdate = (data) => {
    return { 
      type: HOMELIST_VIEW_UPDATE,
      payload: data
    }
  };
  
  module.homeListFailure = (data) => {
    return {
      type: HOMELIST_VIEW_FAILURE,
      payload: data
    }
  };

  return {
    homeLoading: module.homeLoading,
    homeUpdate: module.homeUpdate,
    homeFailure: module.homeFailure,
    homeListLoading: module.homeListLoading,
    homeListUpdate: module.homeListUpdate,
    homeListFailure: module.homeListFailure
  };

} )();
