import axios from 'axios';
import { boleRest, boleMaps, boleUtils } from '../core';
import {
    UPDATE_COUNT,
    NOT_NEWS_LOADING,
    NOT_NEWS_UPDATE,
    NOT_NEWS_DELETE_ELEMENT,
    NOT_NEWS_UPDATE_COUNT,
    NOT_NEWS_FAILURE,
    NOT_CHAT_LOADING,
    NOT_CHAT_UPDATE,
    NOT_CHAT_UPDATE_COUNT,
    NOT_CHAT_FAILURE,
    NOT_PENDING_LOADING,
    NOT_PENDING_UPDATE,
    NOT_PENDING_UPDATE_COUNT,
    NOT_PENDING_FAILURE
} from './types';

export const notification = (function () {
    let module = {};
    let self = module;

    module.updateCount = (data) => {
        return {
            type: UPDATE_COUNT,
            payload: data
        }
    };

    /* Novedades y notificaciones */
    module.notNewsLoading = () => {
        return {
            type: NOT_NEWS_LOADING
        }
    };

    module.notNewsUpdate = (data) => {
        return {
            type: NOT_NEWS_UPDATE,
            payload: data
        }
    };

    module.notNewsDeleteElement = (data) => {
        return {
            type: NOT_NEWS_DELETE_ELEMENT,
            payload: data
        }
    };

    module.notNewsUpdateCount = (data) => {
        return {
            type: NOT_NEWS_UPDATE_COUNT,
            payload: data
        }
    };

    module.notNewsFailure = (data) => {
        return {
            type: NOT_NEWS_FAILURE,
            payload: data
        }
    };

    /* Chat */
    module.notChatLoading = () => {
        return {
            type: NOT_CHAT_LOADING
        }
    };

    module.notChatUpdate = (data) => {
        return {
            type: NOT_CHAT_UPDATE,
            payload: data
        }
    };

    module.notChatUpdateCount = (data) => {
        return {
            type: NOT_CHAT_UPDATE_COUNT,
            payload: data
        }
    };

    module.notChatFailure = (data) => {
        return {
            type: NOT_CHAT_FAILURE,
            payload: data
        }
    };

    /* Firma y documentación pendiente */
    module.notPendingLoading = () => {
        return {
            type: NOT_PENDING_LOADING
        }
    };

    module.notPendingUpdate = (data) => {
        return {
            type: NOT_PENDING_UPDATE,
            payload: data
        }
    };

    module.notPendingUpdateCount = (data) => {
        return {
            type: NOT_PENDING_UPDATE_COUNT,
            payload: data
        }
    };

    module.notPendingFailure = (data) => {
        return {
            type: NOT_PENDING_FAILURE,
            payload: data
        }
    };

    module.getNotifications = () => {

        return (dispatch) => new Promise((resolve, reject) => {
            let url = boleRest.getUrlGetNotifications();
            dispatch(self.notNewsLoading());
            
            axios.get(url).then(response => {
                // TODO. Inyectar el type 'notification' o viene en los datos?
                let dataMapped = boleMaps.mapGetNotifications(response.data, 'notification');
                dispatch(self.notNewsUpdate(dataMapped));
                resolve();
            })
            .catch((error) => {
                console.log('error getNotifications', error);
                let errorData = {
                    hasError: true,
                    msg: boleUtils.setMsgError(error.response ? error.response.status : '')
                };
                dispatch(self.notNewsFailure(errorData));
            });
        });
    };

    module.getNews = () => {

        return (dispatch) => new Promise((resolve, reject) => {
            let url = boleRest.getUrlGetNews();
            dispatch(self.notNewsLoading());

            axios.get(url).then(response => {
                // TODO. Inyectar el type 'News' o viene en los datos?
                let dataMapped = boleMaps.mapGetNotifications(response.data, 'news');
                dispatch(self.notNewsUpdate(dataMapped));
                resolve();
            })
            .catch((error) => {
                console.log('error getNews', error);
                let errorData = {
                    hasError: true,
                    msg: boleUtils.setMsgError(error.response ? error.response.status : '')
                };
                dispatch(self.notNewsFailure(errorData));
            });
        });
    };

    module.acceptNotification = (elementId) => {

        return (dispatch) => new Promise((resolve, reject) => {
            let url = boleRest.getUrlAcceptNotification();
            axios.get(url).then(response => {
                dispatch(self.notNewsDeleteElement(elementId));
                resolve();
            })
            .catch((error) => {
                console.log('error acceptNotification', error);
                let errorData = {
                    hasError: true,
                    msg: boleUtils.setMsgError(error.response ? error.response.status : '')
                };
                dispatch(self.notNewsFailure(errorData));
            });
        });
    };

    return {
        updateCount: module.updateCount,
        notNewsLoading: module.notNewsLoading,
        notNewsUpdate: module.notNewsUpdate,
        notNewsDeleteElement: module.notNewsDeleteElement,
        notNewsUpdateCount: module.notNewsUpdateCount,
        notNewsFailure: module.notNewsFailure,
        notChatLoading: module.notChatLoading,
        notChatUpdate: module.notChatUpdate,
        notChatUpdateCount: module.notChatUpdateCount,
        notChatFailure: module.notChatFailure,
        notPendingLoading: module.notPendingLoading,
        notPendingUpdate: module.notPendingUpdate,
        notPendingUpdateCount: module.notPendingUpdateCount,
        notPendingFailure: module.notPendingFailure,
        getNotifications: module.getNotifications,
        getNews: module.getNews,
        acceptNotification: module.acceptNotification
    };

})();
