import {
	PANEL_VIEW_LOADING,
	PANEL_VIEW_UPDATE_TYPE,
	PANEL_VIEW_HIDE,
	PANEL_VIEW_FAILURE
} from '../actions/types';
import { boleUtils } from '../core/';
  
const initialState = {};

initialState.loading = false;
initialState.error = boleUtils.resetDataError();
initialState.type = '';
initialState.show = false;

const panel = (state = initialState, action = {}) => {

	switch (action.type) {
		case PANEL_VIEW_LOADING:
			return Object.assign({},  state, { error: boleUtils.resetDataError(), loading: true} );
		case PANEL_VIEW_UPDATE_TYPE:
			return Object.assign({},  state, { type: action.payload, loading: false, show: true } );
		case PANEL_VIEW_HIDE:
			return Object.assign({},  state, { type: '', loading: false, show: false, error: boleUtils.resetDataError() } );
		case PANEL_VIEW_FAILURE:
			return Object.assign({},  state, { type: '', error: action.payload, loading: false } );

		default:
			return state;
	}
}

export default panel;