import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { 
	clientChat,
	panel
} from '../../../actions';
import { 
	ButtonNav
} from '../../../components';

class NotificationNews extends Component {

	constructor (props) {
		super(props);
    }

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>

                <div className='notification-news card-item'>
                    <span className='card-item-header'> { this.props.data.title } </span>
                    <p>{ this.props.data.message } </p>
                    {
                        this.props.data.type === 'news' &&
                        <ButtonNav 
                            disable = { false }
                            text = 'CERRAR'
                            handleClick = { () => this.props.closeNotification(this.props.index) }
                        />
                    }
                    {
                        this.props.data.type === 'notification' &&
                        <div>
                            <ButtonNav 
                                disable = { false }
                                text = 'ACEPTAR'
                                handleClick = { () => this.props.acceptNotification(this.props.index) }
                            />
                            <ButtonNav 
                                disable = { false }
                                text = 'RECORDAR MÁS ADELANTE'
                                handleClick = { () => this.props.closeNotification(this.props.index) }
                            />
                        </div>
                    }
                </div>
			</CSSTransitionGroup>
		);
	}
}

NotificationNews.propTypes = {
    index: PropTypes.number,
    data: PropTypes.object,
    closeNotification: PropTypes.func,
    acceptNotification: PropTypes.func
};

const mapStateToProps = state => {
	return {
        notification: state.notification
    };
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(NotificationNews);
