import React from 'react';
// import ReactDOM from 'react-dom';
import { render } from 'react-dom';
import { Provider } from 'react-redux';

import axios from 'axios';
import config from './app/config';
import { boleRest } from './app/core/';

// Necesario para las Promesas de IE
require('babel-polyfill');

// This is the store (state).
import store from './app/store';

import './styles/sass/main.scss';
import App from './app/App';

import * as serviceWorker from './serviceWorker';

// TODO
// ReactDOM.render(<App />, document.getElementById('bole'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

const bole = (function() {

	let module  = {};
	let self = module;

	module.init = () => {
    let urlProperties = self.getUrlProperties();

    axios.get(urlProperties).then(response => {
      config.init(response.data);
      boleRest.init();
			// self.getToken();
			self.renderApp();
    })
    .catch((error) => {
      console.log('error get properties ', error);
    }); 
	};
	
	module.getUrlProperties = () => {
    if ( window.location.href.indexOf('localhost') !== -1 ) {
			console.log('properties1');
      return './assets/bole_dist/properties/properties.json';
    } else {
			console.log('properties2');
      return 'properties.json';
    }
	};
	
	module.renderApp = () => {
    render(
      <Provider store={ store }>
        <App />
      </Provider>,
      document.getElementById('bole')
    );
	};
	
	// TODO
	// module.renderErrorApp = () => {
  //   render(
  //     <Provider store={ store }>
  //       <Modal />
  //     </Provider>,
  //     document.getElementById('bole')
  //   );
	// };
	
	return {
    init: module.init
  }

} )();

window.ReactBole = {
  mount: () => {
      // const el = document.getElementById('bole');
      // ReactDOM.render(<AppRouter />, el);
      bole.init();
  },
  unmount: () => {
      const el = document.getElementById('bole');
      ReactDOM.unmountComponentAtNode(el);
  }
}

