import axios from 'axios';
import config from '../config';

const boleRest = (function () {
	let module = {};
	let self = module;

	module.baseUrlApiBack = '';
	module.baseUrlApiBackFiles = '';
	module.suffix = '';
	module.withOutPagination = '';
	module.standarOrderBy = '';

	module.tokenAutorization = '';
	module.stopRequest = false;

	module.init = () => {
		self.initApiRest();
		self.initInterceptors();
	};

	module.initApiRest = () => {
		// self.withOutPagination = config.getRestParamsWithoutPagination();
		// self.standarOrderBy = config.getRestParamsOrderBy();
		// self.baseUrlApiBack = config.getBaseApiBack();
		// self.baseUrlApiBackFiles = config.getBaseApiBackFiles();
		// self.suffix = config.getSuffixRest();
		// self.stopRequest = false;
	};

	module.initInterceptors = () => {
		axios.interceptors.request.use(function (config) {
			if (!self.stopRequest) {
				config.headers['x-authorization'] = self.tokenAutorization;
				return config;
			} else {
				new axios.Cancel('Operation canceled by the user.');
			}
		}, function (error) {
			return Promise.reject(error);
		});

		axios.interceptors.response.use(function (response) {
			response.headers['x-authorization'] ? self.tokenAutorization = response.headers['x-authorization'] : '';
			return response;
		}, function (error) {
			if (error.response && error.response.status === 401) {
				self.stopRequest = true;
			}
			return Promise.reject(error);
		});
	};

	/* USERS */
	module.getUrlGetUser = () => {
		return self.baseUrlApiBack + 'users' + self.suffix;
	};

	/* OFFICE */
	module.getUrlGetOfficeData = () => {
		return '../assets/bole_dist/mocks/users.json';
		// return self.baseUrlApiBack + 'users' + self.suffix; 
	};
	
	/* CHAT */
	module.getUrlGetChatClient = () => {
		return '../assets/bole_dist/mocks/messages.json';
		// return self.baseUrlApiBack + 'chats/' + idClient + '/messages' + self.suffix + self.standarOrderBy; 
	};

	module.getUrlSendMsgFromUserToClient = () => {
		return '';
		// return self.baseUrlApiBack + 'chats/' + idClient + '/messages' + self.suffix + self.standarOrderBy;
	}
	/* END CHAT */

	/* FILES */
	module.getUrlAddFiles = ()=> {
		return self.baseUrlApiBack + 'files' + self.suffix;
	};

	module.getUrlFiles = ()=> {
	  	return self.baseUrlApiBack + 'files' + self.suffix;
	};
	/* END FILES */

	/* NOTIFICATIONS */
	module.getUrlGetNotifications = () => {
		return '../assets/bole_dist/mocks/notifications.json';
		// return self.baseUrlApiBack + 'chats/' + idClient + '/messages' + self.suffix + self.standarOrderBy; 
	};

	module.getUrlGetNews = () => {
		return '../assets/bole_dist/mocks/news.json';
		// return self.baseUrlApiBack + 'chats/' + idClient + '/messages' + self.suffix + self.standarOrderBy; 
	};

	module.getUrlAcceptNotification = () => {
		return '../assets/bole_dist/mocks/news.json';
		// return self.baseUrlApiBack + 'chats/' + idClient + '/messages' + self.suffix + self.standarOrderBy; 
	};
	/* END NOTIFICATIONS */

	/* SIGN */
	module.getUrlFilesSign = () => {
		return '../assets/bole_dist/mocks/sign.json';
		// return self.baseUrlApiBack + 'chats/' + idClient + '/messages' + self.suffix + self.standarOrderBy; 
	};
	/* END SIGN */

	return {
		init: module.init,
		getUrlGetUser: module.getUrlGetUser,
		getUrlGetOfficeData: module.getUrlGetOfficeData,
		getUrlGetChatClient: module.getUrlGetChatClient,
		getUrlSendMsgFromUserToClient: module.getUrlSendMsgFromUserToClient,
		getUrlAddFiles: module.getUrlAddFiles,
		getUrlFiles: module.getUrlFiles,
		getUrlGetNotifications: module.getUrlGetNotifications,
		getUrlGetNews: module.getUrlGetNews,
		getUrlAcceptNotification: module.getUrlAcceptNotification,
		getUrlFilesSign: module.getUrlFilesSign
	}

})();

export default boleRest;