import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { 
	clientChat,
	panel
} from '../../../actions';
import {
	ButtonNav
} from '../../../components/';

class NotificationPending extends Component {

	constructor (props) {
		super(props);

		this.state = {
			panelType: ''
		};

		this.triggerPanel = this.triggerPanel.bind(this);
	}

	triggerPanel(panelType) {
		this.props.panelUpdate(panelType);
		this.setState({ panelType: panelType });
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='notification-pending card-item'>
					<span className='card-item-header'> POR REALIZAR </span>
					<ul className='card-item-list'>
						<li>
							<p><span>4</span> Firmas </p>
							<ButtonNav 
								disable = { false }
								text = 'Firmar'
								handleClick = { () => this.triggerPanel('sign') }
							/>
						</li>
						<li>
							<p><span>4</span> Documentos</p>
							<ButtonNav 
								disable = { false }
								text = 'Adjuntar'
								handleClick = { () => this.triggerPanel('file') }
							/>
						</li>
					</ul>
				</div>
			</CSSTransitionGroup>
		);
	}
}

NotificationPending.propTypes = {
	
};

const mapStateToProps = state => {
	return {};
};

const mapDispatchToProps = dispatch => ({
	panelUpdate: bindActionCreators(panel.panelUpdate, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(NotificationPending);
