import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import classNames from 'classnames';
import { 
	notification
} from '../../../actions/';

class Bullet extends Component {

	constructor (props) {
        super(props);
    }

    render() {
            return (
                <CSSTransitionGroup
                    transitionName='bole-animation-appear'
                    transitionAppear={true}
                    transitionAppearTimeout={500}
                    transitionEnter={false}
                    transitionLeave={false}>
                    <div className={classNames('bullet', {'open': this.props.home.homeList.show})}>
                        <span>{ this.props.notification.count }</span>
                        <button onClick={ this.props.triggerNotificationList }></button>
                    </div>
                </CSSTransitionGroup>
            );
    }
}

Bullet.propTypes = {
    triggerNotificationList: PropTypes.func
};

const mapStateToProps = state => {
	return {
        notification: state.notification,
        home: state.home
    };
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(Bullet);
