import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { 
	clientChat,
	panel
} from '../../../actions/';
import { 
	ButtonNav
} from '../../../components/';

class FormCall extends Component {

	constructor (props) {
		super(props);

		this.state = {
			formSuccess: false
		};

		this.sendForm = this.sendForm.bind(this);
	}

	sendForm() {
		this.setState({formSuccess: true});
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='form-call-container'>
					<p onClick={ this.props.goBack }>TE LLAMAMOS</p>
					{
						!this.state.formSuccess &&
							<div className='form-call'>
								<span>Teléfono</span>
								<input type='text' />
								<span>Comentario</span>
								<textarea></textarea>
								<ButtonNav 
									disable = { false }
									text = 'Enviar'
									handleClick = { this.sendForm }
								/>
							</div>
					}
					{
						this.state.formSuccess &&
							<div className='form-call-success'>
								<p>ESTUPENDO!!!</p>
							</div>
					}
				</div>
			</CSSTransitionGroup>
		);
	}
}

FormCall.propTypes = {
	goBack: PropTypes.func
};

const mapStateToProps = state => {
	return {};
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(FormCall);
