import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import {
	clientChat,
	panel,
	home
} from '../../../actions/';
import {
	Bullet,
	InfoOffice,
	NotificationList
} from '../../../components/';

class HomeView extends Component {

	constructor(props) {
		super(props);

		this.state = {
			showHomeList: false,
			showPanel: false,
			userRegister: false
		};

		this.triggerNotificationList = this.triggerNotificationList.bind(this);
	}

	componentWillMount() {
		this.getUserData();
	}

	getUserData() {
		// Cambiar valor para mostrar una vista u otra
		this.setState({ userRegister: true });
	}

	/* Mostramos/Ocultamos el panel del listado de notificaciones y ocultamos los otros paneles auxiliares (chat y firma) */
	triggerNotificationList() {
		this.props.homeListUpdate(!this.state.showHomeList);
		this.setState({ showHomeList: !this.state.showHomeList });
		this.props.panelHide();
		this.props.chatHide();
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='module-home' id='home-container'>

					{/* Zona de notificaciones agrupadas dentro del panel del menú */}
					{
						this.state.showHomeList &&
						<div className='home-section'>
							{ /* Información para los usuarios no registrados */}
							{
								!this.state.userRegister &&
								<InfoOffice />
							}
							{ /* Información para los usuarios registrados. Pantallas 04 y 05 */}
							{
								this.state.userRegister &&
								<NotificationList
									type = 'menu' 
								/>
							}
						</div>
					}

					{/* Zona de notificaciones flotantes. No se mostrarán si se despliega el HomeList */}
					{
						!this.state.showHomeList &&
						<div className='notification-section'>
							<NotificationList 
								type = 'notification'
							/>
						</div>
					}

					{ /* Bola que abre el panel */}
					<Bullet
						triggerNotificationList={this.triggerNotificationList}
					/>
				</div>
			</CSSTransitionGroup>
		);
	}
}

HomeView.propTypes = {

};

const mapStateToProps = state => {
	return {
		home: state.home,
		notification: state.notification
	};
};

const mapDispatchToProps = dispatch => ({
	panelHide: bindActionCreators(panel.panelHide, dispatch),
	chatHide: bindActionCreators(clientChat.chatHide, dispatch),
	homeListUpdate: bindActionCreators(home.homeListUpdate, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(HomeView);
