import {
	SIGN_LOADING,
	SIGN_SUCCESS,
	SIGN_FAILURE
} from '../actions/types';
import { boleUtils } from '../core/';
  
const initialState = {};

initialState.loading = false;
initialState.error = boleUtils.resetDataError();
initialState.data = '';

const sign = (state = initialState, action = {}) => {

	switch (action.type) {
		case SIGN_LOADING:
			return Object.assign({},  state, { error: boleUtils.resetDataError(), loading: true} );
		case SIGN_SUCCESS:
			return Object.assign({},  state, { data: action.payload, loading: false } );
		case SIGN_FAILURE:
			return Object.assign({},  state, { data: '', error: action.payload, loading: false } );
		default:
			return state;
	}
}

export default sign;