import axios from 'axios';
import config from '../config';
import { clientChat } from './client-chat.js';
import {
    boleMaps,
    boleRest,
    boleUtils
} from '../core';
import {
    ADD_FILE_LOADING,
    ADD_FILE_SUCCESS,
    ADD_FILE_FAILURE,
    GET_FILES_LOADING,
    GET_FILES_SUCCESS,
    GET_FILES_FAILURE,
    DOWNLOAD_FILE_LOADING,
    DOWNLOAD_FILE_SUCCESS,
    DOWNLOAD_FILE_FAILURE
} from './types';

import store from '../store';
// import { ui } from './';

export const files = (function () {
    let module = {};
    let self = module;

    module.addFilesLoading = () => {
        return {
            type: ADD_FILE_LOADING
        }
    };

    module.addFilesSuccess = (data) => {
        return {
            type: ADD_FILE_SUCCESS,
            payload: data
        }
    };

    module.addFilesFailure = (data) => {
        return {
            type: ADD_FILE_FAILURE,
            payload: data
        }
    };

    module.addFiles = (file, clientId, userId, fbSendMsg) => {
        return (dispatch) => {
            let data = new FormData();
            let url = boleRest.getUrlAddFiles();

            data.append('file', file);
            data.append('clientId', clientId);
            // Descomentar en el caso de mandar a mano el duration del audio
            // if(audioDuration) {
            //   data.append('audioDuration', audioDuration);
            // }

            dispatch(self.addFilesLoading());

            if (config.getEnvironment() === 'mock') {
                self.addFilesMock(dispatch);
            } else {
                axios.post(url, data).then(response => {
                    let atachments = self.mapAddFiles(response.data);
                    let newMsg = {};

                    newMsg.message = config.getStringFileToserver() + file.name;
                    newMsg.clientId = clientId;
                    newMsg.userId = userId;
                    newMsg.timestamp = Date.now();

                    console.log("response FILE: ", response);
                    console.log("FILE newMsg: ", newMsg);

                    dispatch(self.addFilesSuccess(atachments));
                    fbSendMsg(newMsg, atachments);
                })
                .catch((error) => {
                    console.log('error addFiles', error);
                    let errorData = {
                        hasError: true,
                        msg: boleUtils.setMsgError(error.response ? error.response.status : '')
                    };
                    dispatch(self.addFilesFailure(errorData));
                    // store.dispatch(ui.showModalError(errorData.msg, error));
                });
            }
        }
    };

    module.addFilesMock = (dispatch) => {
        let atachments = [{
            'downloadUrl': './mocks/prueba.jpg',
            'fileId': 12,
            'name': 'nameFile',
            'preview': 'stringFile'
        }];

        console.log('atachments: ', atachments);

        // Insertamos el fichero en la conversación
        clientChat.sendMsgFromUserToClientMock(dispatch, atachments);
        // Actualizamos el store de ficheros
        dispatch(self.addFilesSuccess(atachments));
    };

    module.mapAddFiles = (response) => {
        return [response];
    };

    module.getFilesLoading = () => {
        return {
            type: GET_FILES_LOADING
        }
    };

    module.getFilesSuccess = (data) => {
        return {
            type: GET_FILES_SUCCESS,
            payload: data
        }
    };

    module.getFilesFailure = (data) => {
        return {
            type: GET_FILES_FAILURE,
            payload: data
        }
    };

    module.getFiles = (clientId) => {

        return (dispatch) => {

            let url = boleRest.getUrlFiles();

            dispatch(self.getFilesLoading());
            axios.get(url, {
                params: {
                    clientId: clientId,
                    sortBy: 'uploadDate'
                }
            }).then(response => {
                let dataMapped = boleMaps.mapGetFiles(response.data, clientId);
                dispatch(self.getFilesSuccess(dataMapped));
            })
            .catch((error) => {
                let errorData = {
                    hasError: true,
                    msg: boleUtils.setMsgError(error.response ? error.response.status : '')
                };
                dispatch(self.getFilesFailure(errorData));
                // store.dispatch(ui.showModalError(errorData.msg, error));
            });
        }
    };

    /* ===================================================================
        Download file
      =================================================================== */

    // module.downloadFileLoading = () => {
    //     return {
    //         type: DOWNLOAD_FILE_LOADING
    //     }
    // };

    // module.downloadFileSuccess = (data) => {
    //     return {
    //         type: DOWNLOAD_FILE_SUCCESS
    //     }
    // };

    // module.downloadFileFailure = (data) => {
    //     return {
    //         type: DOWNLOAD_FILE_FAILURE,
    //         payload: data
    //     }
    // };

    /* DEPRECATED. Borrar más adelante */
    // module.downloadFile = (fileToDownload) => {
    //     return (dispatch) => {
    //         let url = boleRest.getUrlDownloadFile(fileToDownload);
    //         let name = fileToDownload.name;
    //         let buff = boleMaps.checkIsMobile() ? {} : { responseType: 'arraybuffer' };

    //         dispatch(self.downloadFileLoading());
    //         if (boleMaps.checkIsMobile()) {
    //             window.open(url);
    //         } else {
    //             axios.get(url, buff).then(response => {
    //                 FileDownload(response.data, name);
    //                 dispatch(self.downloadFileSuccess());
    //             })
    //             .catch((error) => {
    //                 console.log('downloadFile', error);
    //                 let errorData = {
    //                     hasError: true,
    //                     msg: boleUtils.setMsgError(error.response ? error.response.status : '')
    //                 };
    //                 dispatch(self.downloadFileFailure(errorData));
    //                 // store.dispatch(ui.showModalError(errorData.msg, error, config.getGeneralErrorCode()));
    //             });
    //         }
    //     };

    // };

    return {
        getFiles: module.getFiles,
        addFiles: module.addFiles
    };

})();
