import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { 
	clientChat,
	panel
} from '../../../actions';
import { 
	ButtonNav
} from '../../../components';

class NotificationChat extends Component {

	constructor (props) {
		super(props);
    }

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>

                <div className='notification-chat card-item'>
                    <span className='card-item-header'> MENSAJES </span>
                    <span>4</span><span>Mensajes de tu gestor</span><span>10:30</span>
                    <p>Están pendientes los documentos...</p>
                    <div>
                        {/* <ButtonNav 
                            disable = { false }
                            text = 'ACEPTAR'
                            handleClick = { () => this.props.acceptNotification(this.props.index) }
                        /> */}
                        <ButtonNav 
                            disable = { false }
                            text = 'RECORDAR MÁS ADELANTE'
                            handleClick = { () => this.props.closeNotification(this.props.index) }
                        />
                    </div>
                </div>
			</CSSTransitionGroup>
		);
	}
}

NotificationChat.propTypes = {
    index: PropTypes.number,
    data: PropTypes.object,
    closeNotification: PropTypes.func,
    acceptNotification: PropTypes.func
};

const mapStateToProps = state => {
	return {
        notification: state.notification
    };
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(NotificationChat);
