import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { Emojione } from 'react-emoji-render';
import classNames from 'classnames';

class ClientChatMessage extends Component {

	componentWillMount() {
		
	}

	getFileName(attachment) {
		return (attachment && attachment.name) ? attachment.name.replace(".", "_").replace(" ", "") : attachment;
	}

	getDataAttachment(attachment, index) {
		return <a className='bole-message-attach'
			key = { index }
			href= { attachment.downloadUrl }
			download>
			<i className='bole-ico bole-ico-paperclip'></i> { attachment.name }
		</a>;
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div id='chat-container-message'>
					{
						this.props.chat.dateSeparate && 
							<h3 className='bole-date-msg'>
								{ this.props.chat.dateSeparate }
							</h3>
					}
					{
						this.props.chat.separateMsgClient && !this.props.chat.dateSeparate &&
							<span className="bole-message-separate"></span>
					}
					<span className={classNames({'container-message--in': this.props.chat.isUserMsg, 'container-message--out': !this.props.chat.isUserMsg})}>
						<section className={classNames({'bole-message bole-message--in': this.props.chat.isUserMsg, 'bole-message bole-message--out': !this.props.chat.isUserMsg })}>
							<div className='bole-message-body'>
								{
									this.props.chat.hasAttachments && 
									this.props.chat.attachments.map( (attachment, index) =>
										<div className={ this.getFileName(attachment) } key = { index }>
											{ 
											attachment &&
												this.getDataAttachment(attachment, index) 
											}
										</div>
									)
								}
								{
									!this.props.chat.hasAttachments && 
									this.props.chat.message.split("<br />").map((data, key) => {
										return <Emojione key={ key } className='bole-message-text' text={ data } />;
									})
								}
								<time className={classNames('bole-message-timestamp')}> { this.props.chat.time ? this.props.chat.time : this.props.chat.date }</time>
							</div>
						</section>
					</span>
				</div>
			</CSSTransitionGroup>
		);
	}
}

ClientChatMessage.propTypes = {
	key: PropTypes.number,
	chat: PropTypes.object,
	totalMsg: PropTypes.number
};

const mapStateToProps = state => {
	return {
	};
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientChatMessage);
