import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import io from 'socket.io-client';
import classNames from 'classnames';
import config from './config';
import '../styles/sass/main.scss';
import { 
  clientChat,
  home,
  notification
} from './actions/';

import {
  ClientChatView,
  HomeView,
  PanelView
} from './components/';

class App extends Component {

  constructor (props) {
    super(props);
    this.socket;
  }

  componentWillMount() {
    /* Mostramos el home para que se pueda mostrar el 'bullet' */
    this.props.homeUpdate(true);

    /* Consultamos las notificaciones/novedades */
		this.props.getNotifications();
    this.props.getNews();
    
    /* Arrancamos el socket */
    this.socketCommunication();

    // TODO. Simulamos que tenemos de base estas notificaciones
    this.props.notChatUpdate(true);
    this.props.notPendingUpdate(true);

  }

  socketCommunication(dataManager) {
    let urlSocket           = config.getUrlSocket();
    let optionsSocket       = {};
    let prefixPath          = config.getSocketPrefixPath();
    let socketPath          = prefixPath + 'socket.io';
    // let clientData          = {};
    // let programaClienteData = '';

    // optionsSocket.reconnection            = true;
    // optionsSocket.reconnectionDelay       = 3000;
    // optionsSocket.reconnectionDelayMax    = 5000;
    // optionsSocket.reconnectionAttempts    = 5;
    // optionsSocket.transports              = ['websocket'];
    // optionsSocket.path                    = socketPath;

    // if(this.state.isMobile) {
    //   programaClienteData = 'AQWW0071';
    // } else if(this.props.userData && this.props.userData.config && this.props.userData.config.tipo_origen == '73'){
    //   programaClienteData = 'AQE20073';
    // } else if(this.props.userData && this.props.userData.config && this.props.userData.config.tipo_origen != '73'){
    //   programaClienteData = 'AQE20017';
    // }

    // console.log("this.props.userData: ", this.props.userData);

    // clientData.application                = 'E2'; // NEO
    // clientData.programaCliente            = programaClienteData; // En móvil AQWW0071 y para escritorio, si el tipo de origen es 73 AQE20073 y para el resto de casos de escritorio AQE20017
    // clientData.tags                       = {}; // No es necesario para el muro al ser 1:1
    // // clientData.clientid                   = 21926; // TODO. Cambiar luego x el del cliente real
    // clientData.clientid                   = dataManager.userId.slice(1); 

    // console.log("SOCKET configuracion clientData: ", clientData);

    urlSocket     = 'http://localhost:4000';
    this.socket = io.connect(urlSocket, optionsSocket);

    this.socket.on('connect', ()=> {
      console.log('conectado');
      // this.socket.emit('storeClientData', clientData);     
      this.socketListeners();
    });

    this.socket.on('connect_failed', ()=> {
      console.warn('Error en la conexión por socket');
      // this.props.showModalError('Error en la conexión por socket', {}, config.getGeneralErrorCode());
    });
  }

  socketListeners() {
    this.socket.on('error', ()=> {
      console.warn('Error en la comunicación por socket');
      // this.props.showModalError('Error en la comunicación por socket', {}, config.getGeneralErrorCode());
    });

    this.socket.on('emitedMsg', (data)=> {
      console.log('mensaje recibido', data);
      // TODO. Revisar socket hasta que se confirme funcionalidad
      this.checkNotification(data);
    });
  }

  /**
   * Controlamos el valor que nos llega del socket para mostrar una notificación u otra
   */
  checkNotification(data) {
    // Simulamos que llega un mensaje:
    // 1.- Se podrá hacer haciendo que se actualice simplemente la conversación
    this.props.getChatClient();
    // 2.- Se introduce el mensaje directamente en la conversación

    // TODO. Hay que enviar el identificador que nos proporcione el socket?
    // Simulamos entrada de notificación
    this.props.getNotifications();
  }

  render() {
		return (
			<div className={classNames('App', {'panel-open': this.props.panel.type && this.props.panel.type.length})}>
        {
          this.props.clientChat.show && 
            <ClientChatView />
        }
        {
          this.props.home.show && 
            <HomeView />
        }
        {
          this.props.panel.type && this.props.panel.type.length &&
            <PanelView />
        }
      </div>
		);
  }
}

const mapStateToProps = state => {
  return {
    clientChat: state.clientChat,
    home: state.home,
    panel: state.panel
  };
};

const mapDispatchToProps = dispatch => ({
  homeUpdate: bindActionCreators(home.homeUpdate, dispatch),
  getChatClient: bindActionCreators(clientChat.getChatClient, dispatch),
  notChatUpdate: bindActionCreators(notification.notChatUpdate, dispatch),
  notPendingUpdate: bindActionCreators(notification.notPendingUpdate, dispatch),
	getNotifications: bindActionCreators(notification.getNotifications, dispatch),
	getNews: bindActionCreators(notification.getNews, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
