import {
	OFFICE_DATA_LOADING,
	OFFICE_DATA_SUCCESS,
	OFFICE_DATA_FAILURE
} from '../actions/types';
import { boleUtils } from '../core/';
  
const initialState = {};

initialState.loading = false;
initialState.error = boleUtils.resetDataError();
initialState.data = false;

const office = (state = initialState, action = {}) => {

	switch (action.type) {
		case OFFICE_DATA_LOADING:
			return Object.assign({},  state, { error: boleUtils.resetDataError(), loading: true} );
		case OFFICE_DATA_SUCCESS:
			return Object.assign({},  state, { data: action.payload, loading: false } );
		case OFFICE_DATA_FAILURE:
			return Object.assign({},  state, { error: action.payload, loading: false } );

		default:
			return state;
	}
}

export default office;