import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import config from '../../../config';
import {
	ClientChatMessage,
	ClientChatSend,
	ClientChatHeader
} from '../../../components/';
import {
	clientChat,
	files
} from '../../../actions/';
import { boleUtils } from '../../../core';

class ClientChatView extends Component {

	constructor(props) {
		super(props);

		this.controlCounter = null;

		this.state = {
			textareaMessage: '',
			textIsEmpty: false,
			disabledSend: false,
			loadingUpdate: false,
			errorFile: {
				hasError: false,
				msg: ''
			},
			fileMessage: ''
		};

		this.handleInputChange = this.handleInputChange.bind(this);
		this.handleSubmitMessage = this.handleSubmitMessage.bind(this);
		this.checkInputDisabled = this.checkInputDisabled.bind(this);
		this.handleFileInput = this.handleFileInput.bind(this);
	}

	componentWillMount() {
		this.props.getChatClient();
	}

	componentWillReceiveProps(nextProps) {
		/* Controlamos el tiempo de respuesta del servidor para deshabilitar el envio entre medias */
		this.setState({ loadingUpdate: nextProps.clientChat.loadingUpdate });

		if (this.props.clientChat.loadingUpdate && !nextProps.clientChat.loadingUpdate && nextProps.clientChat.errorUpdate.hasError) {
			/* Error al intentar mandar un mensaje. En este caso no vaciamos el contenido del textarea */
		} else if (this.props.clientChat.loadingUpdate && !nextProps.clientChat.loadingUpdate && !nextProps.clientChat.errorUpdate.hasError) {
			this.setState({ textareaMessage: '', textIsEmpty: true });
		}
	}

	handleFileInput(event) {
		let fileUpload;

		/* Por si se sube un fichero de audio, reseteamos la disposición de que la vista esta mostrada al completo (para las precargas de audios) */
		// this.props.updateLoadAudio(false);

		console.log("handleFileInput event: ", event);
		if (event.target) {
			console.log("handleFileInput event.target: ", event.target);
		}
		if (event.target.files) {
			console.log("handleFileInput event.target.files: ", event.target.files);
		}
		if (event.target.files && event.target.files.length > 0) {
			console.log("handleFileInput event.target.files[0]: ", event.target.files[0]);
		}

		/* Aunque de momento enviamos directament el archivo, por si más adelante se
		necesita, lo guardamos tb en el estado */
		this.handleInputChange(event);

		/* Comprobamos el tamaño y el nombre del archivo que se intenta enviar */
		if (this.checkSizeFile(event.target.files[0]) && !this.checkErrorFileName(event.target.files[0]) && !this.checkInvalidFileType(event.target.files[0])) {
			/* Envío */
			fileUpload = event.target.files[0];
			console.log('PRE this.props.addFiles: ', event);
			console.log("PRE fileUpload: ", fileUpload);
			this.props.addFiles(fileUpload);
		} else {
			// TODO. Mostrar mensaje de error
		}


		/* Finalmente, reseteamos */
		this.setState({ fileMessage: '' });
	}

	checkSizeFile(file) {
		if (file.size / 1000 > config.getFilesMaxUpload()) {
			//   this.props.showModalError(config.getFilesErrorSizeMsg());
			return false;
		} else {
			return true;
		}
	}

	checkErrorFileName(file) {
		if (boleUtils.checkTags(file.name)) {
			this.setState({
				errorFile: {
					hasError: true,
					msg: config.getFilesErrorNameMsg()
				}
			});
			return true;
		} else {
			return false;
		}
	}

	checkInvalidFileType(file) {
		if (file.name.toLowerCase().indexOf('.exe') >= 0 ||
			file.name.toLowerCase().indexOf('.bat') >= 0 ||
			file.name.toLowerCase().indexOf('.vbs') >= 0 ||
			file.name.toLowerCase().indexOf('.com') >= 0 ||
			file.name.toLowerCase().indexOf('.dll') >= 0) {
			return true;
		} else {
			return false;
		}
	}

	checkInputDisabled() {
		/* Mientras espera la respuesta del servidor, deshabilitamos el envio de mensajes */
		if (this.state.loadingUpdate) {
			return true;
		}
	}

	handleInputChange(event) {
		this.setState({
			[event.target.name]: event.target.value
		});

		if (event.target.name === 'textareaMessage' && event.target.value) {
			this.setState({ textIsEmpty: false })
		} else {
			this.setState({ textIsEmpty: true })
		}
	}

	handleSubmitMessage(event) {
		let chat = {};
		let attachments = [];

		if (!this.state.textareaMessage || this.state.textareaMessage.trim() === '') {
			this.setState({ textareaMessage: '', textIsEmpty: true });
			return;
		}
		chat.message = this.state.textareaMessage;
		//   chat.clientId = this.props.clientData.data.id;
		//   chat.userId = this.props.userData.data.userId;
		chat.timestamp = Date.now();
		chat.dateSeparate = '';
		chat.separateMsgClient = false;

		/* Comprobamos si había comunicación anterior para indicar o no el dateSeparate */
		if ((this.props.clientChat.data && this.props.clientChat.data.length > 0 &&
			this.props.clientChat.data[this.props.clientChat.data.length - 1] &&
			this.props.clientChat.data[this.props.clientChat.data.length - 1].date !== boleUtils.getDateFromTimestamp(chat.timestamp)) ||
			!this.props.clientChat.data[this.props.clientChat.data.length - 1]) {
			chat.dateSeparate = 'HOY';
		}

		/* Comprobamos si el mensaje anterior era propio para crear separación o no */
		if (this.props.clientChat.data && this.props.clientChat.data.length > 0 &&
			this.props.clientChat.data[this.props.clientChat.data.length - 1] &&
			!this.props.clientChat.data[this.props.clientChat.data.length - 1].isUserMsg) {
			chat.separateMsgClient = true;
		}

		this.props.sendMsgFromUserToClient(chat, attachments);
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				{/* Pantalla 07 - Dashboard Usuario Carterizado - 1 */}
				<div id='chat-container'>
					{/* Header */}
					<ClientChatHeader />
					{/* Messages */}
					{
						this.props.clientChat && this.props.clientChat.loading &&
						/* Loading Component */
						<div className='bole-bg-loader'>
							<div className='bole-loader bole-loader--small --call-lazyload'></div>
						</div>
					}
					{
						/* Load data */
						this.props.clientChat && this.props.clientChat.data && !this.props.clientChat.error.hasError && !this.props.clientChat.errorUpdate.hasError &&
						this.props.clientChat.data.map((chat, index) => <ClientChatMessage
							key = { index }
							disabled = { this.checkInputDisabled() }
							chat = { chat }
							totalMsg = { this.props.clientChat.data.length }
						/>)
					}
					{/* TODO. Hay que controlar los errores */}
					{/* {
						this.props.clientChat && ((this.props.clientChat.error && this.props.clientChat.error.hasError) || (this.props.clientChat.errorUpdate && this.props.clientChat.errorUpdate.hasError)) &&
						<aside className='bole-error bole-error--error'>
							<div className='bole-error-main'>
								<h2 className='bole-error-title bole-error-title-msgchat'> {(this.props.clientChat.errorUpdate && this.props.clientChat.errorUpdate.hasError) ? 'Error al enviar el mensaje' : 'Error al recuperar la conversación'} </h2>
								<button className='bole-btn bole-error-btn' onClick={this.closeModal}>Aceptar</button>
							</div>
						</aside>
					} */}
					{/* Footer */}
					<ClientChatSend
						nameMessage='textareaMessage'
						disabled = { this.checkInputDisabled() }
						valueMessage = { this.state.textareaMessage }
						eventClickMessage = { this.handleSubmitMessage }
						eventChangeMessage = { this.handleInputChange }
						nameFile = 'fileMessage'
						valueFile = { this.state.fileMessage }
						eventChangeFile = { this.handleFileInput }
					/>
				</div>
			</CSSTransitionGroup>
		);
	}
}

ClientChatView.propTypes = {

};

const mapStateToProps = state => {
	return {
		clientChat: state.clientChat
	};
};

const mapDispatchToProps = dispatch => ({
	getChatClient: bindActionCreators(clientChat.getChatClient, dispatch),
	sendMsgFromUserToClient: bindActionCreators(clientChat.sendMsgFromUserToClient, dispatch),
	addFiles: bindActionCreators(files.addFiles, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientChatView);
