import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { 
	clientChat,
	panel,
	notification
} from '../../../actions';
import {
	InfoChat,
	NotificationPending,
	NotificationNews,
	NotificationChat
} from '../../../components/';

class NotificationList extends Component {

	constructor (props) {
		super(props);
		this.closeNotification = this.closeNotification.bind(this);
		this.acceptNotification = this.acceptNotification.bind(this);
	}

	/* Recogemos la posición de la notificación para eliminarla del store */
	closeNotification(elementId) {
		this.props.notNewsDeleteElement(elementId);
	}

	/* Realizamos una llamada al endpoint para indicar que lo queremos recordar más adelante */
	acceptNotification(elementId) {
		this.props.acceptNotification(elementId);
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='notification card'>
					{
						this.props.type === 'menu' && 
							<div className='card-header'>
								<p>Hola Sandra!</p>
								<span>No tienes tareas pendientes, ¿Te podemos ayudar en algo?</span>
							</div>
					}
					<div className='card-content'>
						{/* TODO. Quitar. Ahora mismo se controlan solo notificaciones/novedades como elementos popup */}
						{/* Elementos que solo se van a mostrar en el menu */}
						{
							this.props.type === 'menu' && 
							<InfoChat />
						}
						{/* Elementos que solo se van a mostrar como notificaciones flotantes */}
						{
							this.props.type === 'notification' && this.props.notification.notChat.show && 
							<NotificationChat />
						} 
						{/* Elementos que se van a mostrar tanto en notificaciones flotantes como en el menu */}
						{
							this.props.notification.notPending.show && 
							<NotificationPending />
						}
						{
							this.props.notification.notNews.data && this.props.notification.notNews.data.length ?
								this.props.notification.notNews.data.map((element, index) => <NotificationNews
									key = { index }
									index = { index }
									data = { element }
									closeNotification = { this.closeNotification }
									acceptNotification = { this.acceptNotification }
								/>) : null
						}
					</div>
				</div>
			</CSSTransitionGroup>
		);
	}
}

NotificationList.propTypes = {
	type: PropTypes.string    // 'menu' o 'notification'
};

const mapStateToProps = state => {
	return {
		notification: state.notification
	};
};

const mapDispatchToProps = dispatch => ({
	notNewsDeleteElement: bindActionCreators(notification.notNewsDeleteElement, dispatch),
	acceptNotification: bindActionCreators(notification.acceptNotification, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(NotificationList);
