import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import {
	clientChat,
	office
} from '../../../actions/';
import {
	DataOffice,
	FormCall
} from '../../../components/';

class InfoOffice extends Component {

	constructor(props) {
		super(props);

		this.state = {
			formCall: false
		};

		this.showFormCall = this.showFormCall.bind(this);
		this.goBack = this.goBack.bind(this);
	}

	showFormCall() {
		this.setState({formCall: true});
	}

	goBack() {
		this.setState({formCall: false});
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bole-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='info-office'>
					{/* Pantalla 03 Dashboard Usuario No Carterizado info basica   y  - 1*/}
					{
						!this.state.formCall && 
							<DataOffice 
								showFormCall = { this.showFormCall }
							/>
					}
					{/* Pantalla 03 Dashboard Usuario Carterizado TE LLAMAMOS - a   y   a - 1*/}
					{
						this.state.formCall && 
							<FormCall 
								goBack = { this.goBack }
							/>
					}
				</div>
			</CSSTransitionGroup>
		);
	}
}

InfoOffice.propTypes = {

};

const mapStateToProps = state => {
	return {};
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(InfoOffice);
