import axios from 'axios';
import { boleRest, boleUtils } from '../core';
import {
    OFFICE_DATA_LOADING,
    OFFICE_DATA_SUCCESS,
    OFFICE_DATA_FAILURE
} from './types';

export const office = (function () {
    let module = {};
    let self = module;

    module.officeDataLoading = () => {
        return {
            type: OFFICE_DATA_LOADING
        }
    };

    module.officeDataSuccess = (data) => {
        return {
            type: OFFICE_DATA_SUCCESS,
            payload: data
        }
    };

    module.officeDataFailure = (data) => {
        return {
            type: OFFICE_DATA_FAILURE,
            payload: data
        }
    };

    module.getOfficeData = (clientId) => {

        return (dispatch) => new Promise((resolve, reject) => {
            let url = boleRest.getUrlGetOfficeData(clientId);
            dispatch(self.officeDataLoading());

            axios.get(url).then(response => {
                dispatch(self.officeDataSuccess(response));
                resolve();
            })
            .catch((error) => {
                console.log('error getCallClient', error);
                let errorData = {
                    hasError: true,
                    msg: boleUtils.setMsgError(error.response ? error.response.status : '')
                };
                dispatch(self.officeDataFailure(errorData));
            });
        });
    };

    return {
        officeDataLoading: module.officeDataLoading,
        officeDataUpdate: module.officeDataUpdate,
        officeDataFailure: module.officeDataFailure,
        getOfficeData: module.getOfficeData
    };

})();
