import {
	PANEL_VIEW_LOADING,
  PANEL_VIEW_UPDATE_TYPE,
  PANEL_VIEW_HIDE,
	PANEL_VIEW_FAILURE
} from './types';

export const panel = (function() {
  let module  = {};
	let self    = module;
	
	module.panelLoading = () => {
    return {
      type: PANEL_VIEW_LOADING
    }
	};
	
	module.panelUpdate = (data) => {
    return { 
      type: PANEL_VIEW_UPDATE_TYPE,
      payload: data
    }
  };
	
	module.panelHide = (data) => {
    return { 
      type: PANEL_VIEW_HIDE
    }
  };
  
  module.panelFailure = (data) => {
    return {
      type: PANEL_VIEW_FAILURE,
      payload: data
    }
  };

  return {
    panelLoading: module.panelLoading,
    panelUpdate: module.panelUpdate,
    panelHide: module.panelHide,
    panelFailure: module.panelFailure
  };

} )();
